import React from "react";
import {Contact} from "./ProfileInfo";
import {Input, Textarea} from "../../common/FormControls/FormsControls";
import {Field, reduxForm} from "redux-form";
import {requiredField} from "../../../utils/validators/validators";
import s from "./ProfileDataForm.module.css";


 const ProfileDataForm = ({handleSubmit}) => {
     return  <form className={s.profileForm}
                    onSubmit={handleSubmit}
     >
         <div>
             <b>FullName</b>:
             <Field placeholder={"Full name"} name={"fullName"}
                    component={Input}
                    validate={[requiredField]}/>
         </div>
         <div>
             <strong> Looking for a job</strong> :
                 <Field placeholder={""} name={"LookingForAGob"}
                        component={Input}
                         type={"checkbox"}/>
         </div>

         <div>
             <b>My professional skills</b>:
             <Field placeholder={"My professional skills"} name={"lookingForAGobDescription"}
                    component={Textarea}
                  />
         </div>

         <div><b>ABOUT ME</b>:
             <Field placeholder={"About me"} name={"aboutMe"}
                    component={Textarea}
             />
             {/*<div>*/}
             {/*    <b>My contacts:</b>{Object.keys(profile.contacts).map(key => {*/}
             {/*    return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]}/>*/}
             {/*})}*/}
             {/*</div>*/}
         </div>
         <div> <button>save</button></div>

     </form>

 }
 const ProfileDataFormReduxForm=reduxForm({form:"edit-profile"})(ProfileDataForm);
export default ProfileDataFormReduxForm;